import React from "react";




export const LenguageButton = ( props ) => {
    const textSpanish = (props.lenguage === 'spanish') ? 'text-dark' : 'text-muted';
    const textEnglish = (props.lenguage === 'english') ? 'text-dark' : 'text-muted';
    return(
        <div>
             <h6 className={`fw-bolder d-inline-block me-2  ${ textSpanish }`}>Span</h6>
                <label className="switch">
                    <input  type="checkbox" onChange={ props.event } checked={ props.mode } />
                    <span className="slider round"></span>
                </label>
            <h6 className={`fw-bolder d-inline-block ms-2 ${ textEnglish }`}>Eng</h6>
        </div>
    );
}