import React from 'react';
import D1 from '../assets/icons/dice-six-faces-one.png';
import D2 from '../assets/icons/dice-six-faces-two.png';
import D3 from '../assets/icons/dice-six-faces-three.png';
import D4 from '../assets/icons/dice-six-faces-four.png';
import D5 from '../assets/icons/dice-six-faces-five.png';
import D6 from '../assets/icons/dice-six-faces-six.png';


const DiceComponent = ( props ) => {
    let img;
    switch( props.number ){
        case 1:
            img = D1;
            break;
        case 2:
            img = D2;
            break;
        case 3:
            img = D3;
            break;
        case 4:
            img = D4;
            break;
        case 5: 
            img = D5;
            break;
        case 6:
            img = D6;
            break;
        default:
            img = '';
            break;
    }

    return(
        <div onClick={ props.click } className={`col-2  pointer shadow rounded ps-1 pb-2 pt-2 pe-1  text-center  mt-2 me-2 mb-2 ${ props.color  }`}>
           <div>
                <img className="dice" src={ img } alt="" />
           </div>
        </div>
    )
}

const DiceListComponent = (props) => {
    return(
        props.data.map((dice)=>{
            return(
                <DiceComponent 
                    key={ dice.id } 
                    click={ ()=> props.event(dice.id) }
                    color={ dice.isHeld ? "bg-dice-active dice-shine" : "bg-white " } 
                    number={ dice.number } 
                />
            )
        })
    )
}

export { DiceComponent, DiceListComponent}