/**
 *   Importamos el componente "Confetti" el cual nos generará una animación 
 *   de Confettis cayendo al ganar el juego. para installar ConfettiComponent 
 *   en un proyecto debemos instalar el paquete ´npm install react-confetti´.
 *   utilizo el useWindowSizeHook o el window.innerWidth (directamente)
 *   para definir el tamaño de la animación del confetti para que sea responsive.
 * 
 */

import React, { useState, useEffect } from 'react';
import Confetti from 'react-confetti';
import { lenguage } from '../data/tenzie.lenguage.js';
import { LenguageButton } from '../components/lenguageButton.component.js';
import { DiceListComponent } from '../components/dice.component.js';
import { nanoid } from 'nanoid'; // nanoid del paquete nanoid, genera un id aleatorio

const TenziesPage = () => {
    
    // ********[ Hooks ] ************/
    // ******************************
    const width = window.innerWidth;
    const height = window.innerHeight;
    const selectedMode = (localStorage.getItem('lenguage') !== null) ? localStorage.getItem('lenguage') : 'spanish';
    const [lenguageConfig, setLenguageConfig] = useState(selectedMode);
    const [ lenguageState, setLenguageState] = useState(selectedMode === 'spanish' ? lenguage.spanish : lenguage.english);
    const [ diceData , setDice ] = useState(()=> allNewDice());
    const [ tenzies, setTenzies ] = useState(false);

    // **** [ Lenguage Config ] ****
    useEffect(()=> {
        localStorage.setItem('lenguage',`${lenguageConfig}`);
        setLenguageState((lenguageState)=>{
            if(lenguageConfig === 'spanish')
                return tenzies ? { ...lenguage.spanish, buttonText: lenguage.spanish.winingText } : lenguage.spanish;
            else
                return tenzies ? { ...lenguage.english, buttonText: lenguage.english.winingText } : lenguage.english;
        })
    } ,[ lenguageConfig, tenzies ]);

    // **** [ Winning Config ] ****
    useEffect(() => {

        for(let dice of diceData){
            if(diceData.every(d => d.isHeld && d.number === dice.number)){
                setLenguageState((leng) => { return {...leng, buttonText:leng.winingText} });
                setTenzies(true);
                break;
            }
        }

    }, [ diceData ]);

    // ******* [ Methods ] ********
    // ****************************

    function handleLenguage () {
        setLenguageConfig((lenguageConfig)=>{
            if(lenguageConfig === 'english')
                return 'spanish';
            else
                return 'english';
        })
    }

    function generateNewDice(){
        // genera un dado con un numero random  y agregamos un id random con nanoid
        const random = Math.floor(Math.random() * 6) + 1;
        return {id: nanoid(), number: random, isHeld: false,}
    }

    function allNewDice (){
        // genera un array de dados
        const arr = [];
        for(let i = 0; i < 10; i++){
            arr.push(generateNewDice());
        }
        return arr;
    }
    
    function rollDice(){
        if( tenzies ){
            setDice(( diceData ) => diceData.map(dice => generateNewDice()))
            setTenzies(false);
        }
        else {
            setDice(( diceData ) => diceData.map(dice => dice.isHeld ? dice : generateNewDice()))
        }
    }

    function holdDice( id ){
        
        // setDice((diceData) => diceData.map(dice => {
        //     return dice.id === id ? { ... dice, isHeld: !dice.isHeld } : dice;
        // }))
        
        setDice((diceData)=>{
            const selectedDice = diceData.findIndex(dice => dice.id === id);
            diceData[selectedDice].isHeld = !diceData[selectedDice].isHeld;
            return [ ...diceData ]
        })
       
    }
    
    return (
        <div className="bg-dark-green container-fluid h-full d-flex align-items-center justify-content-center">
            { 
                tenzies && <Confetti width={ width } height={ height } />
            }
            <div className="card bg-whites col-12 col-lg-5 border-0 shadow-sm p-3 h-min-400">
               <div className="text-center p-3 ">
                    <h4 className="fw-bold">Tenzies</h4>
                    <p className="text-break text-muted h-min-80">
                        {lenguageState.description}
                    </p>
                    <div className='row justify-content-center'>
                        <LenguageButton lenguage={ lenguageConfig } event={ ()=> handleLenguage() }/>
                    </div>
               </div>
               <div className="container">
                    <div className="row justify-content-center">
                        <DiceListComponent event={ holdDice } data={diceData} />
                    </div>
               </div>
               <button onClick={()=> rollDice() } className="btn bg-purple text-white mt-3 mx-auto shadow-sm ">
                    {lenguageState.buttonText}
               </button>
            </div>
        </div>
    )
}

export { TenziesPage }
